
.main-nav {min-height: 50px;
width: 100vw;
margin: 0% auto;
border-top: solid 3px #ef8236; 

box-shadow: 0px 1px 5px #18e02533; 
position: fixed;
z-index: 5;
top: 0%;
left: 84;
background-color: #f8f9f9;
display: flex;
justify-content: center;
align-items: center;

}

.main-nav-dark {min-height: 50px;
    width: 100vw;
    margin: 0% auto;
    border-top: solid 3px #ef8236; 
    /* box-shadow: 0px 1px 5px #00800833;  */
    border-bottom: solid 1.5px rgba(73, 69, 69, 0.34);
    position: fixed;
    z-index: 5;
    top: 0%;
    left: 84;
    background-color: hsl(210,3%,15%);
    display: flex;
    justify-content: center;
    align-items: center;
    
    }
.main-nav-dark .hamberger{
    filter:brightness(0) invert(1);
}    
    
    .change img{
        position:absolute;
        left: 28px;
        top:9.5px;
    }

  

.navbar{
    height: 100%;
    min-width: 85%;
    display: flex;
    align-items: center;
    justify-content:space-between;
}
            
@media (max-width: 1150px) {
    .navbar{
        min-width: 95%;
       
    }
    .navbar-dark{
        min-width: 95%;
       
    }
    #search-input-tag{
        min-width: 87%;
    }
    #search-input-tag-dark{
        min-width: 87%;
    }
  }

@media (max-width:940px){
    .about-pr-tm{
        display: none;
    }
    .about-pr-tm-dark{
        display: none;
    }
    
    
}  

.navbar-parent{
display: flex;
}

@media (max-width:640px){
    .form-class{
        display: none;
    }
    .navbar-parent{
        display: none;
    }
    .search-input{
        display: none;
    }
    .search-input-dark{
        display: none;
    }
    /* .change{
        background-color: blue;
    } */
    
    .change img{
     position:static;
    }
    .hamberger{
        display: block;
    }


    .form-class-dark{
        display: none;
    }
    .navbar-parent-dark{
        display: none;
    }
    .search-input-dark{
        display: none;
    }
    .search-input-dark{
        display: none;
    }
    /* .change{
        background-color: blue;
    } */
    
    .change img{
     position:static;
    }
    .change-dark img{
        position:static;
       }
       
    .main-nav-dark hamberger{
        display: block;
    }
    




}

.nav-logo{
    padding:5px 25px;
}
.nav-logo-dark{
    padding:5px 25px;
fill:red;
}

.nav-items{
    margin:0px 3px;
    font-size:small;
    font-weight:500;
    text-decoration:none;
    color:rgb(69,69,69);
    transition:0.2s;

}

.nav-btn{
    cursor:pointer;
    border-radius: 20px;
    padding:10px 20px;
    text-decoration: none;
    color:rgb(38, 31, 31)
}

.nav-btn-dark{
    cursor:pointer;
    border-radius: 20px;
    padding:10px 20px;
    text-decoration: none;
    color:white;
}

.nav-item:hover{
    background-color: rgb(226,226,226);
}

.nav-item-dark img{
filter:brightness(0) invert(1);
}



.nav-item-dark:hover{
    background-color: rgb(91, 89, 89);
}

.navbar form{
    flex-grow:1;
    padding:0px 12px;
    position:relative;
}

.search-input{
    min-width:90%;
    margin:0;
    padding:8px 10px 8px 32px;
    font-size:13px;
    border:solid 1px #0000003e;
    border-radius:3px;
    
}

.search-input-dark{
    min-width:90%;
    margin:0;
    padding:8px 10px 8px 32px;
    font-size:13px;
    border:solid 1px rgba(255, 255, 255, 0.672);  
    border-radius:3px;
    background-color: hsl(210,3%,15%);

    
}

.invert_search_icon{
filter: invert(100%);
}


.navbar form .search-icon{
    /* position:absolute;
    left:25px;
    top:8px; */
}

.navbar form{
    display:flex;
}


.nav-links{
    padding:7px 13px;
    border:solid 1px blue;
    border-radius:3px;
    background-color: #e7f8fe;
    cursor:pointer;
    text-decoration: none;
    color:rgb(45, 41, 41);
    
}

.nav-links-dark{
    padding:7px 13px;
    /* border:solid 1px blue; */
    border-radius:3px;
    background-color: hsl(210, 90%, 64%);
    cursor:pointer;
    text-decoration: none;
    color:white;
    
}

.nav-links:hover{
    background-color: #d3e4eb;
}


.nav-links-dark:hover{
    background-color: #86d1ff;
}


.change{
    display: flex;
    justify-content: space-between;
}

